import { combineReducers } from 'redux'

const initiaFabulous = {1:20,2:10,3:0,4:15,5:20,6:6}

const initialComment = [
    {
        id:'1',
        name:'peter',
        text:' React  Meteor  SPA  ',
        postId:'1'
    },
    {
        id:'2',
        name:'billie',
        text:'',
        postId:'1'
    },
    {
        id:'3',
        name:'peter',
        text:'React ',
        postId:'1'
    },
    {
        id:'4',
        name:'peter',
        text:' JS ',
        postId:'2'
    },
    {
        id:'5',
        name:'billie',
        text:'',
        postId:'2'
    },
]

const fabulous = (state = initiaFabulous, action ) => {
    switch(action.type){
        case 'ADD_FABULOUS':
        const { postId } = action
        return { ...state,
            [postId]:state[postId] +1
        }
        default:
         return state
    }
}

const reply = (state = initialComment, action) => {
    switch(action.type){
        case 'ADD_COMMENT':
          return [...state,action.comment]
        case 'REMOVE_COMMENT':
          return action.newReply
        default:
          return state
    }
}

const comments = combineReducers({
    fabulous,
    reply
})

export default comments


